body 
  { margin: 0
  ; padding: 0
  ; font-family: sans-serif
  }

button
, input[type="button"]
, input[type="reset"]
, input[type="submit"]
  { -webkit-appearance: button
  ; cursor: pointer
  }

button
, select 
  { text-transform: none
  }

button
, input
, optgroup
, select
, textarea 
  { color: inherit
  ; font: inherit
  ; margin: 0
  }

button
, .button
, [type=submit]
  { margin: .3em 0
  ; cursor: pointer
  ; -webkit-transition: all 0.3s
  ; -o-transition: all 0.3s
  ; transition: all 0.3s
  ; border-radius: .2em
  ; height: auto
  ; -webkit-box-shadow: 0 0 transparent inset; box-shadow: 0 0 transparent inset
  }

button
, .button
, [type=submit]
, [type=reset]
, [data-button]
  { display: inline-block
  ; text-align: center
  ; margin: 0
  ; padding: .3em .9em
  ; vertical-align: middle
  ; background: rgb(34, 34, 34)
  ; color: #fdfdfd
  ; border: 0
  ; border-radius: .2em
  ; width: auto
  ; -webkit-touch-callout: none
  ; -webkit-user-select: none
  ; -moz-user-select: none
  ; -ms-user-select: none
  ; user-select: none
  }

         button.ok
,       .button.ok
, [type=submit].ok
,  [type=reset].ok
, [data-button].ok
  { background: rgb(31, 135, 196);}

         button.no
,       .button.no
, [type=submit].no
,  [type=reset].no
, [data-button].no
  { background: rgb(168, 33, 146);}


         button.big
,       .button.big
, [type=submit].big
,  [type=reset].big
, [data-button].big
  { font-size: 2em;}
  
           button.disabled,        button:disabled
  ,       .button.disabled,       .button:disabled
  , [type=submit].disabled, [type=submit]:disabled
  ,  [type=reset].disabled,  [type=reset]:disabled
  , [data-button].disabled, [data-button]:disabled
  , :disabled
  { background: rgb(179, 179, 179) }


input
, textarea
, .select
, select 
  { line-height: 1.5
  ; margin: 0
  ; height: 2.1em
  ; padding: .3em .6em
  ; border: 1px solid #ccc
  ; background-color: #fff
  ; border-radius: .2em
  ; -webkit-transition: all 0.3s
  ; -o-transition: all 0.3s
  ; transition: all 0.3s
  ; width: 100%
  }

a
, a:visited
, a:active
  { color:inherit
  ; text-decoration: none
  }

a:hover
  { text-decoration:underline
  }


