.event{
  /* display: flex; */
  max-width:42em;
  -ms-flex-align:start;
      align-items:flex-start;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-direction:column;
      flex-direction:column;
  padding:1em;
  /* display:grid; */
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: 
    "a right"
    "b right"
    "c right"
    "d right"
    "controls controls";
  border-top:1px solid #ccc;
  /* margin-top:1.58rem; */
}

.info{
  max-width: 100%;
}

.title{
  composes:info;
  grid-area: a;
}

.date{
  composes:info;
  color:#7a7a7a;
  grid-area: b;
}

.rank{
  grid-area: c;
}

.description{
  composes:info;
  font-size:.8em;
  margin-bottom:.3em;
  grid-area: d;
}

.image{
  width:10em;
  height:3em;
  float:right;
  grid-area: right;
  -ms-flex-item-align: end;
      align-self: flex-end;
  justify-self: end;
  -ms-flex-item-align: start;
      align-self: start;
  visibility: hidden;
}


.event:first-of-type .info{
  width:50%;
}

.event:first-of-type .image{
  width:19em;
  height:13em;
  float:right;
}

.event:first-of-type .image, :global .edit-mode :local .image{
  visibility: visible;
}


.event::after{
  content:'';
  display: table;
  width:100%;
  clear:both;
  height:0;
}

@media screen and (max-width:60em){
  .image{
    display:none;
  }
}

.title, .date, .description{
  margin-bottom:.5rem;
}


