.toastsContainer{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: fixed;
  top:1em;
  right:1em;
  bottom:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.toast{
  padding:1em;
  /* position: relative; */
  -webkit-animation: drop 1s ease-out;
          animation: drop 1s ease-out;
  -webkit-transition:all .4s ease-out;
  -o-transition:all .4s ease-out;
  transition:all .4s ease-out;
  width:22em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin:.2em;
  right:0;
  top:0;
}

@-webkit-keyframes drop{
  0% { -webkit-transform: translateY(-100em); transform: translateY(-100em); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes drop{
  0% { -webkit-transform: translateY(-100em); transform: translateY(-100em); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

.inside{
  padding:1em;
  background:#fdfdfd;
  width:100%;
  height:100%;
  position: relative;
  -webkit-box-shadow: 3px 3px 4px rgba(0,0,0,.4);
          box-shadow: 3px 3px 4px rgba(0,0,0,.4);
}

.ok{
  background:green;
}

.no{
  background:red;
}

.neutral{

}

.close{
  position: absolute;
  top:0;
  right:0;
  background:transparent;
}


